	module.exports = {"data":{"markdownRemark":{"html":"<h2>機能概要</h2>\n<p> Hi-macho(ハイマッチョ)はEZweb公式の出会い掲示板「ひまっちゅ(ファミリービズ(株)提供)」からインスパイヤした、EZweb用出会い掲示板CGIです。Perl 5.005以降で動作し、機能性と使いやすさ、そして筋肉質に長けています。  </p>\n<h2>特徴</h2>\n<p>  <img src=\"https://danmaq.com/wp-content/uploads/2013/11/macho.gif\"></p>\n<ul>\n<li>\n<p>シンプルで使いやすい掲示板機能 掲示板は5つのスレッドに分かれていてその日の気分によって書き込み場所を変えたりもできます。</p>\n</li>\n<li>\n<p>マッチョメール機能 1ユーザーに1つHimaccho専用私書箱「マッチョメール」が与えられます。ここで他の筋肉質仲間に果たし状を送りつけたり受け取ったり出来ます。</p>\n</li>\n<li>\n<p>マッチョメール通知機能 いつ返事がくるか分からない果たし状。いちいちアクセスせずともEメールで通知してくれる親切機能。</p>\n</li>\n<li>\n<p>友達登録機能 せっかく出来た仲間も連絡手段がなければ見知らぬ人同然。仲良くなったらここに登録！これでいつでも果たし状を送れます！</p>\n</li>\n<li>\n<p>友達検索機能 これでシャイなあの娘も見逃さない！掲示板に書かない人もこの検索機能で一発捕獲。筋肉仲間増殖機能の最終兵器！</p>\n</li>\n<li>\n<p>迷惑ユーザー隔離機能 こんなひ弱なやつ仲間でもなんでもない！そんな奴とはさっさとこの隔離機能を使って縁を切ってしまいましょう。ただ使いすぎは周りのご迷惑となりますので……</p>\n<h2>その他</h2>\n</li>\n<li>\n<p>サービス期間:2001/3～2002/12</p>\n</li>\n<li>\n<p>その後もCGIの頒布は2009年まで継続。</p>\n<h2>このスクリプトの頒布は終了しました。</h2>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2001-03-01T00:00:00+09:00","strDate":"2001/2/28","title":"Hi-macho - EZweb向け出会い掲示板システム"}}},"pathContext":{"path":"/ja/blog/2001/03/01-hi-macho/","langKey":"ja"}}

